import { Injectable, Injector } from '@angular/core';
import { HttpService } from '@ecp-caf/caf-common';
import { Observable } from 'rxjs';

@Injectable()
export class TaskActionImplService {

  constructor(
    private injector: Injector,
    private http: HttpService
  ) { }

  /**
   * è·åä»»å¡ä¸­å¿éç½®é¡¹
   * @param code éç½®é¡¹çCode
   */
  getTaskCenterConfigByCode(code: string, ownerType?: string, ownerId?: string): Observable<any> {
    let url = `/api/runtime/task/v1.0/task-center-setting/code?code=${code}`;
    ownerType && (url += `&ownerType=${ownerType}`);
    ownerId && (url += `&ownerId=${ownerId}`);
    return this.http.get(url);
  }

  /**
    * æ·»å å³æ³¨
    * @param sourceId sourceId
    */
  addFocus(sourceId: string): Observable<any> {
    const url = `/api/runtime/task/v1.0/focus?sourceId=${sourceId}`;
    return this.http.post(url, '');
  }

  /**
   * ç§»é¤å³æ³¨
   * @param processInstanceId æµç¨å®ä¾id
   */
  removeFocus(processInstanceId: string): Observable<any> {
    const url = `/api/runtime/task/v1.0/focus?processInstanceId=${processInstanceId}`;
    return this.http.delete(url, '');
  }

  /**
   * æ¯å¦å³æ³¨äºè¯¥ä»»å¡
   * @param processInstanceId æµç¨å®ä¾id
   */
  ifFocused(processInstanceId: string): Observable<any> {
    const url = `/api/runtime/task/v1.0/focus/?processInstanceId=${processInstanceId}`;
    return this.http.get(url);
  }
}
