import { Component, OnInit, ViewChild, ViewContainerRef, Injector, ComponentFactoryResolver, ComponentRef, ComponentFactory, Input, Output, EventEmitter, ElementRef, NgZone } from '@angular/core';
import { TaskExecuteComponent } from '@gsp-wf/wf-task-handler';
import { TaskAction, TaskEntity, ActionBaseImplComponent, SubmitResult, ActionEvent } from '@gsp-wf/task-impl-api';
import { Observable, Subject } from 'rxjs';
import { LoadingService } from '@farris/ui-loading';
import { MessagerService } from '@farris/ui-messager';
import { ActionOrigin } from '@gsp-wf/task-impl-api';
import { TaskActionImplService } from '../task-action-impl.service';

declare var System: any;

@Component({
  selector: 'task-action-impl',
  templateUrl: './task-action-impl.component.html',
  styleUrls: ['./task-action-impl.component.scss'],
})
export class TaskActionImplComponent implements OnInit {

  @ViewChild('container', { read: ViewContainerRef }) container: ViewContainerRef;
  @Input() actions: TaskAction[];
  @Input() submitName: string;
  @Input() showButtons = true; //æ¯å¦å±ç¤ºæäº¤æé®ãä»»å¡åççé¢å±ç¤ºï¼ä»»å¡åè¡¨ä¸å±ç¤º
  @Input() batch = false;
  @Input() task: TaskEntity;

  _iframe: ElementRef<HTMLIFrameElement>;
  @Input()
  set iframe(iframe: ElementRef<HTMLIFrameElement>) {
    this._iframe = iframe;
    if (this.iframe && this.componentRef) {
      this.componentRef.instance.iframe = this.iframe;
    }
  }

  get iframe() {
    return this._iframe;
  }
  @Input() origin: string; // æ¥æºï¼ä»åªæå¼ç

  @Output() submited = new EventEmitter<SubmitResult>();
  @Output() actionCode = new EventEmitter<any>();

  selectAction: TaskAction;
  actionCodeMap = new Map();
  componentRef: ComponentRef<ActionBaseImplComponent>;

  index = 0;
  submitDisable: boolean = false; // æäº¤æé®æ¯å¦ç¦ç¨
  ifFocused: boolean = false; // æ¯å¦å³æ³¨äºæ­¤ä»»å¡
  focus: string = this.i18nService("focus");//å³æ³¨æ­¤æµç¨
  ifShowFocus: boolean = true; // æ¯å¦å¯ç¨æçå³æ³¨åè½
  dialogHeight: number; // ä»»å¡åè¡¨ä¸å¼¹æ¡çé«åº¦
  dialogWidth: number; // ä»»å¡åè¡¨ä¸å¼¹æ¡çå®½åº¦

  ngZone: NgZone;

  // æ³¨æï¼ä¿®æ¹æé æ¹æ³éè¦éæ°æåtask-mainåtask form
  constructor(
    private injector: Injector,
    private cfr: ComponentFactoryResolver,
    private loadingService: LoadingService,
    private msgService: MessagerService,
    private taskService: TaskActionImplService
  ) {
    this.ngZone = this.injector.get(NgZone);
  }

  ngOnInit() {
    //è¡¨åè§¦åé¢è§£æï¼éæ°å è½½ç»ä»¶
    (window as any).gspWfEventService = (window as any).gspWfEventService || {};
    (window as any).gspWfEventService.simulate = (func: Function) => {
      this.ngZone.run(() => {
        this.actionSelect(this.actions[0], 0).subscribe(() => func && typeof func === 'function' && func());
      })
    };
    // æ¯å¦å¯ç¨æçå³æ³¨åè½
    const load = this.loadingService.show();
    this.taskService.getTaskCenterConfigByCode("If_Show_My_Focus", 'all', '*').subscribe(re => {
      load.close();
      re && re.currentValue === 'false' && (this.ifShowFocus = false);
      // æ¯å¦å³æ³¨äºè¯¥ä»»å¡(ä»è¯¦ææå¼)
      if (this.ifShowFocus && this.task && this.showButtons) {
        const load = this.loadingService.show();
        this.taskService.ifFocused(this.task.processInstanceId).subscribe(re => {
          load.close();
          this.ifFocused = re && re.length ? true : false;
        }, () => {
          load.close();
        });
      }
    }, () => {
      load.close();
    });
  }

  /**
   * éä¸­å¨ä½æé®
   * @param action 
   * @param index 
   * @returns 
   */
  actionSelect(action: TaskAction, index?: number): Observable<any> {
    let subject = new Subject();
    this.selectAction = action;
    if (index) {
      this.index = index;
    }
    let cmpFactory: ComponentFactory<ActionBaseImplComponent>;
    if (action.typeId === 'wf') {
      cmpFactory = this.cfr.resolveComponentFactory(TaskExecuteComponent);
      this.loadComponentFactory(cmpFactory);
      setTimeout(() => {
        subject.next({ result: true });
      }, 100);
    } else {
      System.import(action.impl + '?v=' + new Date().getTime().toString()).then((moduleFactories: any) => {
        const moduleFactory: any = Object.values(moduleFactories)[0];
        const moduleRef = moduleFactory.create(this.injector);
        cmpFactory = moduleRef.instance.getActionComponentFactory(action);
        this.loadComponentFactory(cmpFactory);
        subject.next({ result: true });
      });
    }
    return subject.asObservable();
  }

  /**
   * å¨æå è½½å®¡æ¹ç»ä»¶
   * @param cmpFactory 
   */
  private loadComponentFactory(cmpFactory: ComponentFactory<ActionBaseImplComponent>) {
    let comment;
    if (this.componentRef && this.componentRef.instance) {
      comment = (this.componentRef.instance as any).comment;
    }
    this.container.clear();
    this.componentRef = this.container.createComponent(cmpFactory, 0, this.injector);
    this.componentRef.instance.origin = this.showButtons ? ActionOrigin.TaskForm : ActionOrigin.TaskCenter;
    if (this.task && this.componentRef.instance.setTask) {
      this.componentRef.instance.setTask(this.task);
    }
    if (this.componentRef.instance.setAction) {
      this.componentRef.instance.setAction(this.selectAction);
    }
    if (this.componentRef.instance.dialogHeight) {
      this.dialogHeight = this.componentRef.instance.dialogHeight;
    }
    if (this.componentRef.instance.dialogWidth) {
      this.dialogWidth = this.componentRef.instance.dialogWidth;
    }
    // æå­wfçå®¡æ¹æè§
    (this.componentRef.instance as any).wfCacheComment = comment;
    this.container.insert(this.componentRef.hostView);
  }

  /**
   * ç¹å»æäº¤
   */
  submit() {
    //è¡¨ååäºä»¶
    if (this.showButtons && window['taskCenterBeforeLoad']) {
      this.submitDisable = true;
      const context = {
        task: this.task,
        action: this.selectAction
      }
      window['taskCenterBeforeLoad'](context).then(re => {
        this.submitDisable = false;
        if (re && re.result == true) {
          this.showConfirmDialog();
        }
      }, error => {
        this.submitDisable = false;
        console.error(error);
      })
    } else {
      this.showConfirmDialog();
    }
  }

  /**
   * æ¯å¦æäº¤åç¡®è®¤
   */
  private showConfirmDialog() {
    this.taskService.getTaskCenterConfigByCode('Is_Confirm_Commit').subscribe((config: any) => {
      if (config.currentValue === 'true') {
        this.msgService.question(this.i18nService("confirm") + this.selectAction.name + 'ï¼', () => {
          this.executeSubmit();
        });
      } else {
        this.executeSubmit();
      }
    });
  }

  private executeSubmit() {
    this.submitDisable = true;
    if (this.selectAction.preEvents && this.selectAction.preEvents.length > 0
      && this.selectAction.preEvents.filter(e => !e.batch).length > 0) {
      let taskComment: string;
      const action = this.selectAction as TaskAction;
      if (action.typeId === 'wf') {
        // åæè§æ¡éçæè§å­æ¾å¨taskCommenté
        taskComment = (this.componentRef.instance as any).comment;
      } else {
      }
      this.executeEvents([...this.selectAction.preEvents.filter(e => !e.batch)], () => {
        this.confirmSubmit(taskComment);
      }, taskComment);
    } else {
      this.confirmSubmit();
    }
  }

  private confirmSubmit(taskComment?: string) {
    const load = this.loadingService.show();
    this.handleSubmit().subscribe((re: any) => {
      if (re) {
        load.close();
        if (re.result) {
          if (this.selectAction && this.selectAction.postEvents.length > 0
            && this.selectAction.postEvents.filter(e => !e.batch).length > 0) {
            this.executeEvents([...this.selectAction.postEvents.filter(e => !e.batch)], () => {
              this.submited.emit(re);
              setTimeout(() => {
                this.submitDisable = false;
              }, 2000);
            }, taskComment);
          } else {
            this.submited.emit(re);
            setTimeout(() => {
              this.submitDisable = false;
            }, 2000);
          }
        } else {
          this.submitDisable = false;
        }
      }
      this.actionCodeMap.set('isTransfer', this.selectAction.code === 'Transfer');
      this.actionCodeMap.set('isAddSignFront', this.selectAction.code === 'AddSignFront');
      this.actionCodeMap.set('isUnClaim', this.selectAction.code === 'Unclaim');
      this.actionCodeMap.set('isComment', this.selectAction.code === 'Comment');
      this.actionCodeMap.set('isClaim', this.selectAction.code === 'Claim');
      this.actionCode.emit(this.actionCodeMap);
    });
  }


  handleSubmit(): Observable<SubmitResult> {
    return this.componentRef.instance.submit();
  }

  batchHandleSubmit(tasks: TaskEntity[]) {
    return this.componentRef.instance.batchSubmit(tasks);
  }

  private executeEvents(events: ActionEvent[], handler: any, taskComment?: string) {
    if (events && events.length > 0) {
      const event = events.shift();
      const url = event.eventImpl.split('#')[0];
      const func = event.eventImpl.split('#')[1];
      System.import(url + '?v=' + new Date().getTime().toString())
        .then((moduleFactories: any) => {
          const moduleFactory: any = Object.values(moduleFactories)[0];
          const moduleRef = moduleFactory.create(this.injector);
          const context: any = {
            task: this.task,
            action: this.selectAction,
            origin: 'TaskForm',
            formIframe: this.iframe,
            comment: taskComment,
          };
          moduleRef.instance[func].apply(moduleRef.instance, [context]).subscribe((re: any) => {
            // å¼å®¹æ§çå¸å°ç±»ååæ°
            if (typeof re === 'boolean') {
              if (re) {
                this.executeEvents(events, handler, taskComment);
              } else {
                this.submitDisable = false;
              }
            } else {
              if (re.result) {
                if (re.comment) {
                  taskComment = re.comment;
                  // åå°çåäºä»¶æè§åå«å¨taskCommenté
                }
                this.executeEvents(events, handler, taskComment);
              } else {
                this.submitDisable = false;
              }
            }
          });
        });
    } else {
      (this.componentRef.instance as any).comment = taskComment;
      handler();
    }
  }

  /**
   * ç¹å»æ·»å /åæ¶å³æ³¨
   */
  focusTask() {
    const load = this.loadingService.show();
    if (this.ifFocused) {
      this.taskService.addFocus(this.task.sourceId).subscribe(() => {
        load.close();
      }, () => {
        load.close();
      });
    } else {
      this.taskService.removeFocus(this.task.processInstanceId).subscribe(() => {
        load.close();
      }, () => {
        load.close();
      });
    }
  }

  /**
   * å½éå
   * @param code 
   * @returns 
   */
  i18nService(code: string) {
    var result;
    const supportLanguage = ["zh-CHS", "zh-CHT", "en"];
    const curLanguage = localStorage.getItem("languageCode");
    const translate = {
      "zh-CHS": {
        focus: "å³æ³¨æ­¤æµç¨",
        focused: "å·²å³æ³¨æ­¤æµç¨",
        confirm: "ç¡®è®¤"
      },
      "zh-CHT": {
        focus: "éè¨»æ­¤æµç¨",
        focused: "å·²éè¨»æ­¤æµç¨",
        confirm: "ç¢ºèª"
      },
      en: {
        focus: "Focus this process",
        focused: "Focused this process",
        confirm: "Confirm"
      },
    };
    if (curLanguage && supportLanguage.includes(curLanguage)) {
      result = translate[curLanguage][code];
    } else {
      result = translate["zh-CHS"][code];
    }
    return result;
  }



}
