import { NgModule } from '@angular/core';
import { TaskActionImplComponent } from './task-action-impl/task-action-impl.component';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { WfTaskHandlerModule } from '@gsp-wf/wf-task-handler';
import { LoadingModule } from '@farris/ui-loading'
import { ThrottleClickDirective } from './throttleTime/throttleClickDirective';
import { MessagerModule } from '@farris/ui-messager'
import { TaskActionImplService } from './task-action-impl.service';

@NgModule({
  declarations: [
    TaskActionImplComponent,
    ThrottleClickDirective
  ],
  imports: [
    CommonModule,
    FormsModule,
    WfTaskHandlerModule,
    LoadingModule,
    MessagerModule.forRoot()
  ],
  exports: [TaskActionImplComponent],
  providers: [
    TaskActionImplService,
  ]
})
export class TaskActionImplModule { }
